<template>
    <div id="common-box">
        <div class="user-box">
            <h4>已报考考试信息</h4>
            <div class="test-form">
                <el-table
                    height="420"
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column align="center" label="考试全称" prop="exam.examName"></el-table-column>
                    <el-table-column align="center" label="考试类型" prop="exam.examType"></el-table-column>     
                    <el-table-column align="center" label="考试等级" prop="exam.examGrade"></el-table-column>     
                    <el-table-column align="center" label="考试当天日期" prop="exam.examDate"></el-table-column>     
                    <el-table-column align="center" label="报到时间段" prop="exam.arriveTime"></el-table-column>
                    <el-table-column align="center" label="报名状态" prop="applicationStatus"></el-table-column>
                    <el-table-column align="center" label="审核状态" prop="enrollmentStatus"></el-table-column>
                    <el-table-column align="center" label="缴费状态" prop="paymentStatus"></el-table-column>                   
                    <el-table-column align="center" label="操作">
                        <template slot-scope="scope">
                            <el-button type="success" size="small" @click="handleEdit(scope.row)">缴费</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <!-- 底部导航 -->
            <div class="pagination">
                <Pagination
                :totalMsg="query.totalMsg"
                :pageIndex="query.pageIndex"
                :pageSize="query.pageSize"
                @setPageIndex="getPageIndex"
                @setPageSize="getPageSize"></Pagination>
            </div>
            
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
    components:{
        Pagination
    },
    data(){
        return {
            query:{
                pageSize:5,
                pageIndex:1,
                totalMsg:null,
            },
            exams:{},
            tableData:[],
            userinfo:{}
        }
    },
    methods:{
        getPageSize(val){
            this.query.pageSize=val;
            this.getExamList();
        },
        getPageIndex(val){
            this.query.pageIndex=val;
            this.getExamList();
        },
        getExamList(){//获取考试信息
            let that=this;
            this.$api.stusign({
                pageNum:1,
                pageSize:10
            }).then(res=>{
                that.tableData=res.data.signs.list;
            })
        },
        handleEdit(info){
            let that=this;
            if(info.paymentStatus === "已缴费"){
                return that.$message.error('这项考试您已完成缴费');
            }
            this.$api.putpay(info.id).then(res=>{
                if(res.err_code === 100){
                    that.$message.success('缴费成功');
                    that.getExamList();
                }else{
                    that.$message.error('缴费失败');
                }
            })
        },
    },
    created(){
        this.getExamList();
    }
}
</script>

<style lang="scss" scoped>
    .user-box{
        margin: 0 auto;
        height: 540px;
        width: 100%;
        background: #fff;
        border: 1px solid #e5e5e5;
        box-sizing: border-box;
        padding: 20px;
        
    }
    .pagination{
        float: right;
        margin: 10px 20px;
    }
</style>